.bulletinBoard {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
}
.bulletinBoard__form,
.bulletinBoard__messageList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: var(--div-margin);
  text-decoration: none;
  text-align: end;
  width: 40%;
}
.bulletinBoard__messageList {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.bulletinBoard__messageList__message {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin: var(--div-margin);
  text-decoration: none;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.bulletinBoard__messageList__message > p {
  display: -ms-flexbox;
  display: flex;
  margin: 0px 20px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  /* border-bottom: 1px var(--main-light) solid; */
  /* background-color: var(--main-light); */
  min-height: 4rem;
  -ms-flex-align: center;
      align-items: center;
}



