:root {
  /* color */
  --main-dark: #9f875d;
  --main-normal: #d9c6a4;
  --main-light: #e9decc;
  --main-light37: #e9decc37;
  --main-bg: #f6f2ec;
  --white: #ffffff;
  --light-gray: #f1f1f1;
  --black: #000000;

  /* font-size */
  --title-font-size: 2rem;
  --subtitle-font-size: 1.6rem;
  --heading-font-size: 1.4rem;
  --text-font-size: 1.2rem;
  --line-height-multiple: 1.4;

  /* other */
  --border-radius: 5px;
  --div-margin: 0px auto 2rem auto;
  --logo-height: 120px;
  --logo-width: 120px;

  /* RWD size*/
  --mobile-width: 360px;
  --tablet-width: 360px;
}

html {
  font-size: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.common__title {
  font-size: var(--title-font-size);
}
.common__subtitle {
  font-size: var(--subtitle-font-size);
}

.common__heading {
  font-size: var(--heading-font-size);
}
.common__text {
  font-size: var(--text-font-size);
}
.common__font--bold {
  font-weight: bold;
}

.common__block {
  /*區塊+文字置中*/
  text-align: center;
  margin: var(--div-margin);
}
.common__block--bilateral {
  margin: 2rem 0;
}

.common__interval--normal {
  margin-bottom: 2rem;
}
.common__interval--shrink {
  margin-bottom: 1rem;
}

.common__titleBlock {
  margin: 2rem auto;
  width: 90%;
}

.common__pageFrame {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-direction: column;
      flex-direction: column;
}
.common__buttonGroup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.common__buttonGroup Button {
  margin: 0 0 0 10px;
}

.common__hidden {
  display: none;
}

/* button submit style*/
.common__submit {
  background-color: var(--main-normal);
  min-width: 5rem;
  padding: 0.8rem 1.5rem;
  border: 1px solid var(--main-normal);
  border-radius: var(--border-radius);
  color: var(--black);
  cursor: pointer;
  text-align: center;
  white-space: nowrap;
  font-size: var(--heading-font-size);
}

.common__submit:hover {
  background-color: var(--main-light);
  border: 1px solid var(--main-normal);
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}



